import React from 'react';
import { Typography } from '@mui/material';
import MatTooltip from '@mui/material/Tooltip';
import {
  LineChart,
  CartesianGrid,
  Legend,
  Tooltip,
  Line,
  XAxis,
  YAxis,
  ResponsiveContainer
} from 'recharts';
import FallDownIcon from '@mui/icons-material/ArrowDownwardOutlined';
import FlyUpIcon from '@mui/icons-material/ArrowUpwardOutlined';

export function UsersView(props) {
  const [icon, setIcon] = React.useState(<FallDownIcon />);
  const [diff, setDiff] = React.useState(0);

  React.useEffect(() => {
    if (props.dateRegister.length > 0) {
      const lastOne = props.dateRegister[props.dateRegister.length - 1]['افراد ثبت نام شده'];
      const beforeLasOne = props.dateRegister[props.dateRegister.length - 2]['افراد ثبت نام شده'];

      if (lastOne === beforeLasOne) setIcon(<span> </span>);
      else if (lastOne > beforeLasOne) {
        setIcon(<FlyUpIcon color="success" />);
      } else {
        setIcon(<FallDownIcon color="error" />);
      }

      setDiff(lastOne - beforeLasOne);
    }
  }, []);

  return (
    <div className="row">
      <div className="col-12">
        <div className="row">
          <div className="col-12">
            <Typography align="center" className="p-3" color="green">
              تعداد افراد آنلاین {props.onlineUsers}
            </Typography>
            <hr />
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            {!(props.dateRegister.length > 0) ? (
              <Typography align="center" className="py-3" variant="h6">
                تعداد افراد ثبت نام شده : {props.registeredCount}
                <Typography className="py-5">آماری وجود ندارد !</Typography>
              </Typography>
            ) : (
              <>
                <div className="row">
                  <div className="col-12">
                    <Typography align="center" className="py-3" variant="h6">
                      تعداد افراد ثبت نام شده : {props.registeredCount}
                      <MatTooltip title="مقدار رشد/افت به نسبت روز قبل" placement="top">
                        <Typography variant="caption" color={diff >= 0 ? 'green' : 'error'}>
                          {icon}
                          {Math.abs(diff)} {diff >= 0 ? `+` : '-'}
                        </Typography>
                      </MatTooltip>
                    </Typography>
                  </div>
                </div>
                <ResponsiveContainer width={'100%'} height={300}>
                  <LineChart
                    width={700}
                    height={300}
                    data={props.dateRegister}
                    margin={{
                      top: 5,
                      right: 30,
                      // left: 20,
                      bottom: 5
                    }}>
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="date" />
                    <YAxis />
                    <Tooltip />
                    <Legend />
                    <Line
                      type="monotone"
                      dataKey="افراد ثبت نام شده"
                      stroke="#8884d8"
                      activeDot={{ r: 1 }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default UsersView;
